
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #04213f;
}
footer, header
{
  background-color: rgb(83, 126, 218);
  float: left;
  width: 100%;
}

.container {
    border: 2px solid #dedede;
    background-color: rgb(174, 188, 214);
    border-radius: 5px;
    padding: 10px;
    margin: 10px 0;
}
.darker {
    border-color: #ccc;
    background-color: rgb(142, 157, 224);
}
.container::after {
    content: "";
    clear: both;
    display: table;
}
.time-right {
    float: right;
    color: rgb(9, 5, 34);
}
#chatMsgDiv
{
  float: left;
  width: 100%;
}
#currentChatDiv
{
  float: left;
  width: 49%;
}
#chatHistoryDiv
{
  float: left;
  width: 49%;
}
#currentChatWindow
{
  background-color: rgb(39, 97, 173);
  height: 500px;
  width: 99%;
  overflow-y: auto;
}
#chatHistoryWindow
{
  background-color: rgb(39, 97, 173);
  float: right;
  height: 500px;
  width: 99%;
  overflow-y: auto;
}
